
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0.8rem;
}
@charset "UTF-8";
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.flex-item {
  -webkit-box-flex: 1;
  -ms-flex: 1;
      flex: 1;
}

/* 水平居中 */
.h-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

/* 垂直居中 */
.v-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.float-left {
  float: left;
}
.float-right {
  float: right;
}
.clearfloat {
  overflow: hidden;
  zoom: 1;
}
.line-clamp-1 {
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
.line-clamp-2 {
  overflow: hidden;
  -ms-text-overflow: ellipsis;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
}
.hr-7 {
  height: 0.093333rem;
  background: #F6F6F6;
}
.hr-8 {
  height: 0.106667rem;
  background: #F6F6F6;
}
.page-container {
  overflow-y: scroll;
}
.loadmore-wrap {
  overflow-y: scroll;
}

/* 上拉、下拉加载更多动画 */
.mint-loadmore-top span,
.mint-loadmore-bottom span {
  display: inline-block;
  vertical-align: middle;
}

/* 底部按钮 */
.bottom-button {
  width: 100%;
  height: 0.666667rem;
}
.bottom-button button {
  position: fixed;
  width: 100%;
  bottom: 0;
  height: 0.666667rem;
  line-height: 0.666667rem;
  font-size: 0.2rem;
  color: #fff;
  text-align: center;
  border: none;
}

/* 遮罩 */
.mask-wrap {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}
.mask-bg {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

/* 立即结算 */
.payorder-bottombtn-wrap {
  width: 100%;
  height: 0.666667rem;
  background: #F6F6F6;
}
.payorder-bottombtn-wrap .payorder-bottombtn-box {
  position: fixed;
  width: 100%;
  height: 0.666667rem;
  line-height: 0.666667rem;
  bottom: 0;
  border-top: 0.013333rem solid #F6F6F6;
  background: #fff;
}
.payorder-bottombtn-wrap .payorder-bottombtn-box .accounts-money {
  margin-right: 0.293333rem;
  text-align: right;
  font-size: 0.2rem;
}
.payorder-bottombtn-wrap .payorder-bottombtn-box button {
  height: 100%;
  padding: 0 0.6rem;
  color: #fff;
  border: none;
  border-radius: 0;
}

/* CSS Document */

/*reset css*/

/** Set default font family and font color and background color */
html {
  font-family: '\5B8B\4F53',arial, verdana, sans-serif;
  color: #333;
  background: #ffffff;
}

/***** body *****/
body {
  font-size: 100%;
  background: #ffffff;
}

/*****Remove default margin and padding*****/
body,
dl,
h1,
h2,
h3,
h4,
h5,
h6,
form,
p {
  margin: 0;
}
input,
button,
textarea,
td,
th {
  padding: 0;
}
menu,
ul,
ol {
  margin: 0;
  padding: 0;
}

/***** Remove default vertical scrollbar in IE 8/9/10/11 and resize button*****/
textarea {
  resize: none;
  overflow: auto;
}

/***** Improve link behaviour*****/
a {
  text-decoration: none;
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}

/***** Prevent `sub` and `sup` affecting `line-height` in all browsers. *****/
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}

/***** fix border problem *****/
iframe,
img {
  border: 0;
}

/***** remove list style of ul and ol*****/
ul,
ol {
  list-style: none;
}

/***** input and button *****/
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
button:-moz-focusring,
input:-moz-focusring {
  outline: 0.013333rem dotted ButtonText;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
input {
  outline: none;
}
input::-webkit-input-placeholder {
  color: #C0C0C0;
}
input::-moz-placeholder {
  color: #C0C0C0;
}

/* Mozilla Firefox 19+ */
input:-moz-placeholder {
  color: #C0C0C0;
}

/* Mozilla Firefox 4 to 18 */
input:-ms-input-placeholder {
  color: #C0C0C0;
}

/* Internet Explorer 10-11 */

/***** table *****/
table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*****display behaviour *****/
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}

/*****common classes *****/
.clear {
  clear: both;
}

/* 去掉半透明灰色遮罩 */
* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  tap-highlight-color: rgba(0, 0, 0, 0);
}
html {
  -ms-touch-action: none;
      touch-action: none;
}
input,
button,
select,
textarea {
  outline: none;
}
.renew {
  width: 2.666667rem;
  height: 2.666667rem;
  border: 0.013333rem solid #ccc;
}